.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  border: black 1px solid;
  .login-container {
    display: flex;
    flex-direction: column;
    justify-content: center; // mb ubrat'
    align-items: center;
    width: 40%;
    height: 80%;
    border: black 1px solid;

    .input-login {
      display: flex;
      flex-direction: row;

      width: 60%;
      height: 30px;
      margin: 5px 0;
      background-color: aliceblue;
      border-radius: 5px;
      input {
        width: 90%;
        border: none;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        background-color: aliceblue;
      }
    }
    .input-password {
      display: flex;
      flex-direction: row;

      width: 60%;
      height: 30px;
      margin: 5px 0;
      background-color: aliceblue;
      border-radius: 5px;
      input {
        background-color: aliceblue;
        width: 90%;
        border: none;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
      }
    }
    button {
      width: 50%;
      height: 30px;
    }
  }
}

.no-login {
  display: none;
}
