.testing {
  width: 100%;
  height: 500px;
  display: flex;
  flex-direction: row;
  justify-content: end;
  padding-top: 12vh;
}

.content-box {
  width: 75%;
  margin-right: 2vw;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content-box .first-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(34, 157, 157);
  border-radius: 10px;
  margin-bottom: 20px;
  width: 100%;
  height: 42vh;
}
.content-box .first-block h1 {
  text-align: center;
  align-items: center;
}/*# sourceMappingURL=style.css.map */