.header {
  width: 100vw;
  height: 10vh;
  position: fixed;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;

  .header-content {
    background-color: aliceblue;
    border-radius: 10px;
    margin-top: 5px;
    width: 99vw;
    display: flex;
    flex-direction: row;
    .brand-logo {
      width: 20vw;
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
    .information {
      text-align: center;
      flex-grow: 20;
    }
    .profile {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      flex-grow: 1;
      .profile_reg_btns {
        display: flex;
        flex-direction: row;
        .btn-register {
          height: 35px;
          width: 100px;
          margin-right: 20px;
          background-color: #f48023;
          border: none;
          border-radius: 5px;
        }
        .btn-login {
          height: 35px;
          width: 80px;
          background-color: #eaeaea;
          border: none;
          border-radius: 5px;
        }
      }
    }
  }
}
