.app {
  margin: 0;
}

.one {
  background-color: red;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  border: black 1px solid;
}
.login .login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: 80%;
  border: black 1px solid;
}
.login .login-container .input-login {
  display: flex;
  flex-direction: row;
  width: 60%;
  height: 30px;
  margin: 5px 0;
  background-color: aliceblue;
  border-radius: 5px;
}
.login .login-container .input-login input {
  width: 90%;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: aliceblue;
}
.login .login-container .input-password {
  display: flex;
  flex-direction: row;
  width: 60%;
  height: 30px;
  margin: 5px 0;
  background-color: aliceblue;
  border-radius: 5px;
}
.login .login-container .input-password input {
  background-color: aliceblue;
  width: 90%;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.login .login-container button {
  width: 50%;
  height: 30px;
}/*# sourceMappingURL=style.css.map */