.sidebar {
  // background-color: aquamarine;
  width: 21vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // justify-content: end;
  .content {
    background-color: burlywood;
    width: 20vw;
    height: 70%;
    border-radius: 10px;
    margin-top: 1vh;
    margin-left: 0.5vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    .close_sidebar {
      display: flex;
      flex-direction: row;
      justify-content: end;
      width: 95%;
      margin-top: 5px;
      button {
        border-radius: 7px;
        border: none;
        width: 3vw;
        height: 3vw;
      }
    }
    .search {
      // border: black 1px solid;
      background-color: aliceblue;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-top: 5px;
      height: 3vw;
      border-radius: 5px;
      input {
        width: 80%;
        height: 75%;
        border: none;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        background-color: aliceblue;
      }
      button {
        background-color: rgb(226, 230, 234);
        height: 80%;
        border: none;
        border-radius: 5px;
        // border-top-right-radius: 5px;
        // border-bottom-right-radius: 5px;
      }
    }
    .menu {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      .line-container {
        width: 98%;
        display: flex;
        justify-content: start;
        align-items: center;
        background-color: aliceblue;
        height: 40px;
        border-radius: 5px;
        margin: 1px;
        .left-indication-stick {
          background-color: orangered;
          height: 100%;
          width: 5px;
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
        .line-container_img {
          margin-left: 10px;
        }
      }
    }
  }

  .social-links {
    background-color: burlywood;
    width: 20vw;
    margin-top: 1vh;
    margin-left: 0.5vw;
    border-radius: 10px;
    height: 7%;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
